.App {
    font-family: 'Nunito', sans-serif;
    font-size: calc(.2vw + .8vh);
    background-color: #FDFDFB;
    color: #090B0B;
    overflow-x: hidden;
    scroll-behavior: smooth;
}

a {
    text-decoration: none;
    text-decoration-color: black;
    color: inherit;
    font-family: inherit;
}

button {
    background: transparent;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    border: none;
    font-family: 'Nunito', sans-serif;
    font-size: 1.3em;
    padding-bottom: .3em;
    font-weight: bold;
    cursor: pointer;
}

::-webkit-scrollbar {
    width: 10px;
  }
  

  ::-webkit-scrollbar-track {
    background: transparent;
  }
  
  
  ::-webkit-scrollbar-thumb {
      background:  #7A95D1
    }
    
    
    ::-webkit-scrollbar-thumb:hover {
      background: #0033AD;
  }

  .container {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.nav_bar {
  display: grid;
  grid-template-columns: 2vw 10vw 40vw 45vw 3vw;
  overflow: hidden;
  width: 100vw;
  height: 6vh;
  align-items: center;
  background-color: #FDFDFB;
  position:fixed;
  top: 0;
  left: 0;
  z-index: 2;
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 10%), 0 2px 4px -1px rgb(0 0 0 / 6%);
}

@media only screen and (max-width: 1400px) {

  .nav_bar {
      grid-template-columns: 3vw 10vw 15vw 65vw 2vw;
      padding: 2em 0px 0em 0px;
  }

}


.logo {
  grid-column: 2;
  height: auto;
  width: 20vw;
  max-width: 12em;
  margin-bottom: .5em;
}

.nav_buttons {  
 grid-column: 4;
 display: flex;
 justify-content: space-around;   
 z-index: 4;
}


.nav_buttons > button  {
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  overflow: hidden;
  font-family: 'Nunito', sans-serif;
  background: transparent;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  border: none;
  font-size: 1.4em;
  padding-bottom: .3em;
  font-weight: bolder;
  cursor: pointer;
}

.nav_buttons > button:before {
  content: "";
  position: absolute;
  z-index: -1;
  left: 51%;
  right: 51%;
  bottom: 0;
  background: #0033AD;
  height: 2px;
  transition-property: left, right;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

.nav_buttons > button:hover:before, .nav_buttons > button:focus:before, .nav_buttons > button:active:before {
  left: 0;
  right: 0;
}

a {
  text-decoration: none;
  color: inherit;
  font-family: inherit;
}
