.chessboard {
    height: calc(30vh + 10.8vw);
    width: calc(30vh + 10.8vw);
    background-size: contain;
    grid-row: 1/3;
    grid-column: 1/5;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(8,1fr);
    justify-items: center;
    align-items: center;    
}